import { ReactNode } from 'react';
import { Link } from 'react-router-dom';

interface Props {
  href: string;
  children: ReactNode;
  className?: string;
}

export default function LinkTo(props: Props) {
  const { children, href, className } = props;
  return (
    <Link to={href} className={className || ''}>
      {children}
    </Link>
  );
}
