import { ICompanyDetail } from '../../../interfaces';

interface IProps {
  company: ICompanyDetail;
}

export default function CompanyInformation(props: IProps) {
  const { company } = props;
  return (
    <div className="w-[300px]">
      <img src={company.image} alt={company.name} className="w-[300px]" />
      <div className="mt-3">
        <div className="mb-2 flex gap-3">
          <i className="bi bi-geo" />
          <span>{company.address}</span>
        </div>
        {company.contacts.map((contact) => (
          <div className="mb-2 flex gap-3" key={contact.title}>
            <i className={`bi bi-${contact.icon}`} />
            <span>{contact.title}</span>
          </div>
        ))}
      </div>
    </div>
  );
}
